<template>
  <div class="business-details">
    <el-tabs v-model="activeData.index" @tab-change="tabClick">
      <el-tab-pane
        v-for="item in tabList"
        :label="item.label"
        :name="item.value"
        :key="item.value"
      ></el-tab-pane>
    </el-tabs>
    <component :is="activeData.component"></component>
  </div>
</template>

<script lang="ts" setup>
import { reactive, markRaw, defineAsyncComponent } from "vue";

const companyCallQualityInspection = defineAsyncComponent(
  () =>
    import(
      "@views/business-management/osprey-cloud-call/call-quality-testing/company-call-quality-inspection.vue"
    )
);
defineOptions({
  name: "businessManagement.ospreyCloudCall.callQualityTesting.detail",
});
const activeData = reactive({
  index: 1,
  component: markRaw(companyCallQualityInspection),
});

const tabList = [
  {
    label: "公司通话质检",
    value: 1,
    componentName: markRaw(companyCallQualityInspection),
  },
];
const tabClick = (value: any) => {
  activeData.index = value;
  activeData.component = tabList[value - 1].componentName;
};
</script>
<style lang="scss" scoped>
.business-details {
  position: relative;
  :deep(.el-tabs__header) {
    margin: 0;
  }
  :deep(.el-tabs__nav-wrap)::after {
    display: none;
  }
}
</style>
